<template>
<div>
  <div class="profile">
      <div class="login">
          <span class="title">Profilni tahrirlash</span>

            <div class="image_box">
                <img :src="one_user.image ? baseURL + '/public/images/' + one_user.image : require('@/assets/profile-picture.jpg')" alt="person_image"
                class="person_image" @click="$refs.fileInput.click()">
                <input type="file" style="display: none;" ref="fileInput" @change="onFileSelected">
            </div>

            <ValidationObserver ref="formProfileChange" v-slot="{ handleSubmit, invalid }">
                <form class="" @submit.prevent="handleSubmit(onSubmit1)">


                <div class="mt-5">
                    <span class="text-black ml-1">Login</span>
                    <ValidationProvider :rules="{required: true,
                    error: [errorStatus, errorMessage, errorPlace, 'email']}" v-slot="{ errors }">
                        <v-text-field class="mt-2" outlined
                            v-model="profile.login" :error-messages="errors"
                            placeholder="Misol: 182455">
                        </v-text-field>
                    </ValidationProvider>
                </div>

                <div>
                    <span class="text-black ml-1">Ism Sharif</span>
                    <ValidationProvider :rules="{required: true,
                     error: [errorStatus, errorMessage, errorPlace, 'name']}" v-slot="{ errors }">
                        <v-text-field class="mt-2" outlined
                            v-model="profile.fullname" :error-messages="errors"
                            placeholder="Misol: 182455">
                        </v-text-field>
                    </ValidationProvider>
                </div>


                <div class="d-flex justify-space-between">
                    <v-btn
                            class="primaryBtn btn"
                            elevation="0"
                            :loading="loading"
                            type="submit"
                            :disabled="invalid"
                            @click="loader = 'loading'"
                        >
                        <img src="@/assets/icons/shield-tick-white.svg"
                        alt="shield-tick-white" width="22">
                        <span class="ml-1 fs-16px">Saqlash</span>
                        </v-btn>
                </div>

                </form>
            </ValidationObserver>
      </div>


      <div class="password">
          <span class="title">Parolni o`zgartirish</span>

            <ValidationObserver ref="formProfilePasswordChange" v-slot="{ handleSubmit, invalid }">
              <form class="" @submit.prevent="handleSubmit(onSubmit2)">

                <div class="mt-4">
                    <span class="text-black ml-1  ">Joriy parol</span>
                    <ValidationProvider :rules="{required: true,
                    error: [errorStatus, errorMessage, errorPlace, 'password']}"
                     v-slot="{ errors }">
                        <v-text-field class="mt-2" outlined  maxlength="12"
                            :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                            :type="show1 ? 'text' : 'password'"
                            counter
                            @click:append="show1 = !show1"
                            v-model="password.current" :error-messages="errors">
                        </v-text-field>
                    </ValidationProvider>
                </div>

                <div>
                    <span class="text-black ml-1">Yangi parol</span>
                    <ValidationProvider :rules="{required: true,
                     error: [errorStatus, errorMessage, errorPlace, 'new_password']}"
                     v-slot="{ errors }">
                        <v-text-field :error-messages="errors" maxlength="12"
                            :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                            :type="show2 ? 'text' : 'password'"
                            counter
                            @click:append="show2 = !show2"
                            v-model="password.new" min="1"
                            outlined class="mt-2">
                        </v-text-field>
                    </ValidationProvider>
                </div>


                <div>
                    <span class="text-black ml-1">Yangi parol(tasdiqlash)</span>
                    <ValidationProvider :rules="{required: true,
                     error: [errorStatus, errorMessage, errorPlace, 'new_password_confirm']}"
                      v-slot="{ errors }">
                        <v-text-field :error-messages="errors" maxlength="12"
                            :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                            :type="show3 ? 'text' : 'password'"
                            counter
                            @click:append="show3 = !show3"
                            v-model="password.confirm_new" min="1"
                            outlined class="mt-2">
                        </v-text-field>
                    </ValidationProvider>
                </div>

                <div class="d-flex justify-start">
                    <v-btn
                        class="primaryBtn btn"
                        elevation="0"
                        :loading="loading"
                        type="submit"
                        :disabled="invalid"
                        @click="loader = 'loading'"
                    >
                    <img src="@/assets/icons/shield-tick-white.svg"
                      alt="shield-tick-white" width="22">
                    <span class="ml-1 fs-16px">Saqlash</span>
                    </v-btn>
                </div>

            </form>
          </ValidationObserver>
      </div>
  </div>
</div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import { BASE_URL } from '@/plugins/variables'

export default {
    props: [''],
    mixins: [loaderMixin],
    components: {
    },
    data(){
        return{
            // ============= SECONDARY_DATA =============
            baseURL: '',
            // profile: {
            //   photo: '',
            //   login: '',
            //   fullname: ''
            // },

            password:{
              current: '',
              new: '',
              confirm_new: ''
            },

            selectedFile: null,
            show1: false,
            show2: false,
            show3: false,
            // ============= SECONDARY_DATA =============



            // ============= PRIMARY_DATA =============
            errorStatus: false,
            errorMessage: '',
            errorPlace: ''
            // ============= PRIMARY_DATA =============

        }
    },
    watch: {
        // ============= NEW_IP START =============
        'profile': {
            handler(value){
                if(value){
                    this.clearErrors()
                }
            }, deep: true,
        },
        'password': {
            handler(value){
                if(value){
                    this.clearErrors()
                }
            }, deep: true,
        },
        // '$store.state.main.userActive': {
        //     handler: async function(newValue) {
        //         if(newValue){
        //             await this.refresh()
        //             this.$store.commit('main/SET_UPDATE_USER_ACTIVE', false)
        //         }
        //     },
        //     deep: true,
        //     immediate: true
        // },
        // ============= NEW_IP END =============






        // ============= OTHER_WATCHERS START =============
        async selectedFile(newValue, oldValue){
            if(newValue){
                await this.onUpload();
            }
        },
        // ============= OTHER_WATCHERS END =============


    },
    async mounted(){
        // await this.refresh();
        this.baseURL = BASE_URL
    },
    computed: {
        ...mapState(
            {
                profile: state => state.main.profile,
                user: state => state.main.user,
                one_user: state => state.main.one_user,
            }
        ),


    },
    methods:{
        ...mapActions(
            {
                updateUserLogin: 'main/updateUserLogin',
                updateUserPassword: 'main/updateUserPassword',
                updateUserPhoto: 'main/updateUserPhoto',
                fetchOneUser: 'main/fetchOneUser'
            }
        ),

        async onFileSelected(event){
            this.selectedFile = event.target.files[0]
        },

        async onUpload(){
            const fd = new FormData();
            fd.append('image', this.selectedFile, this.selectedFile.name)

            let a = await this.updateUserPhoto({
                id: this.one_user.id,
                image: fd,
            })


            if (a.response && a.response.status == 422) {
                if(a.response.data.errors.image){
                    this.errorMessage = a.response.data.errors.image[0];
                    this.errorPlace = 'image'
                }
                this.errorStatus = true;
                this.$toast.error(this.errorMessage);
            }else if(a.response && a.response.status == 409){
                if(a.response.data.message){
                    this.errorMessage = a.response.data.message;
                }
                this.$toast.error(this.errorMessage);
            }else{
                await this.refresh()

                this.$toast.success('Muvaffaqiyatli qo`shildi!');
                this.closeModal();
            }
        },




        // ============= REFRESH START =============
        // async refresh(){
        //     this.loading = true
        //     await this.fetchOneUser(this.user.user_id)

        //     this.profile.photo = this.one_user.image
        //     this.profile.login = this.one_user.email
        //     this.profile.fullname = this.one_user.name

        //     this.loading = false
        // },
        // ============= REFRESH START =============










        // ============= CLEARS_AND_CLOSES START =============
        clearInputs(){
            this.clearErrors()
        },
        clearErrors(){
            this.errorStatus = false;
            this.errorMessage = '';
            this.errorPlace = '';
        },
        closeModal(){
            this.clearInputs();
            console.log(this.$parent.ipAddDialog)
            this.$parent.ipAddDialog = false;
            this.$refs.formIpAdd.reset();
        },
        // ============= CLEARS_AND_CLOSES END =============














        // ============= ON_SUBMIT1 START =============
        async onSubmit1(){
            this.$refs.formProfileChange.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============

                    // ============= UPDATING_USER START =============
                    let a = await this.updateUserLogin({
                        id: this.one_user.id,
                        name: this.profile.fullname,
                        email: this.profile.login,
                    })

                    if (a.response && a.response.status == 422) {
                        if(a.response.data.errors.name){
                            this.errorMessage = a.response.data.errors.name[0];
                            this.errorPlace = 'name'
                        }else if(a.response.data.errors.password){
                            this.errorMessage = a.response.data.errors.password[0];
                            this.errorPlace = 'password'
                        }
                        this.errorStatus = true;
                        this.$toast.error(this.errorMessage);
                    }else if(a.response && a.response.status == 409){
                        if(a.response.data.message){
                            this.errorMessage = a.response.data.message;
                        }
                        this.$toast.error(this.errorMessage);
                    }else{
                        await this.refresh()

                        this.$toast.success('Muvaffaqiyatli qo`shildi!');
                        this.closeModal();
                    }
                    // ============= UPDATING_USER END =============
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })
        },
        // ============= ON_SUBMIT1 END =============








        // ============= ON_SUBMIT2 START =============
        async onSubmit2(){
            this.$refs.formProfilePasswordChange.validate().then(async(success) => {
                if(success){
                    // ============= IF SUCCESS =============

                    if(this.password.new != this.password.confirm_new){
                            this.errorMessage = 'Parollar bir biriga mos kelmayapti!';
                            this.errorPlace = 'new_password_confirm'
                            this.errorStatus = true;
                            this.$toast.error(this.errorMessage);
                    }else{
                        // ============= UPDATING_USER START =============
                        let a = await this.updateUserPassword({
                            id: this.one_user.id,
                            old_password: this.password.current,
                            password: this.password.new,
                        })

                        if (a.response && a.response.status == 422) {
                            if(a.response.data.errors.name){
                                this.errorMessage = a.response.data.errors.name[0];
                                this.errorPlace = 'name'
                            }else if(a.response.data.errors.password){
                                this.errorMessage = a.response.data.errors.password[0];
                                this.errorPlace = 'password'
                            }
                            this.errorStatus = true;
                            this.$toast.error(this.errorMessage);
                        }else if(a.response && a.response.status == 409){
                            if(a.response.data.message){
                                this.errorMessage = a.response.data.message;
                            }
                            this.$toast.error(this.errorMessage);
                        }else{
                            await this.refresh()

                            this.$toast.success('Muvaffaqiyatli qo`shildi!');
                            this.closeModal();
                        }
                        // ============= UPDATING_USER END =============
                    }
                }else{
                    // ============= IF NO SUCCESS =============
                    this.$toast.error('Xato yuz berdi!');
                }
            })
        },
        // ============= ON_SUBMIT2 END =============









    }

}
</script>


